/* MODAL STYLE */

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1050;
  display: none;
  overflow: hidden;
  left: 0;
}

@media (min-width: 784px) {
  .modal {
    left: 36px;
  }
}

@media (min-width: 961px) {
  .modal {
    left: 160px;
  }
}

.fade {
  opacity: 0;
  transition: opacity .15s linear;
}

.fade.in {
  opacity: 1;
}

.fade.in .modal-dialog {
  margin-top: 40px;
}

.modal-dialog {
  position: relative;
  transition: all 0.35s linear;
  max-width: 840px;
  width: 100%;
  margin-top: 0px;
  margin-left: auto;
  margin-right: auto;
}

.modal-content {
  position: relative;
  background-color: #fff;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  outline: 0;
  -webkit-box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
  box-shadow: 0 3px 9px rgba(0, 0, 0, 0.5);
}

.modal-header {
